
.cat-slider {
  width: 300px;
}
.at-markdown {
  padding: 0 40px 120px;
  margin-left: -1px;
  width: 100%;
  min-height: 500px;
  overflow: hidden;
  border-left: 1px solid #e9e9e9;
}
a:hover {
  color: #3da8f5;
  -webkit-transition: color .3s ease;
  transition: color .3s ease;
}



